import HexagonCard from "./Hexagon";

const WhyChooseUs = () => {
  const features = [
    {
      icon: "/icon1.png",
      title: "Registered & Certified Professionals",
      description:
        "We are registered tax practitioners ensuring compliance and accuracy.",
    },
    {
      icon: "/icon2.png",
      title: "Trusted Industry Experience",
      description:
        "Years of experience in accounting, taxation, and compliance.",
    },
    {
      icon: "/icon3.png",
      title: "Hassle-Free Tax Lodgment",
      description:
        "We make tax return filing quick, easy, and stress-free for businesses.",
    },
    {
      icon: "/icon4.png",
      title: "Strict Data Security Measures",
      description:
        "We implement advanced data protection for safeguarding your data.",
    },
    {
      icon: "/icon5.png",
      title: "Personalized Support & Advice",
      description:
        "Our team offers tailored financial solutions for your needs.",
    },
    {
      icon: "/icon6.png",
      title: "Melbourne-Based, Client-Focused",
      description:
        "We build long-term relationships with our clients in the local community.",
    },
    {
        icon: "/icon6.png",
        title: "Melbourne-Based, Client-Focused",
        description:
          "We build long-term relationships with our clients in the local community.",
      },
      {
        icon: "/icon6.png",
        title: "Melbourne-Based, Client-Focused",
        description:
          "We build long-term relationships with our clients in the local community.",
      },
  ];

  return (
    <section className="text-center py-12">
      <h2 className="text-3xl font-bold">
        Lodge your Tax Return with Convenience and we will Assure Compliance
      </h2>
      <button className="mt-4 bg-[#FE5722] text-white px-6 py-2 rounded-full text-lg font-bold">
        WHY CHOOSE US?
      </button>

      {/* Hexagon Grid Layout */}
      <div className="grid grid-cols-2 md:grid-cols-5 gap-6 mt-10 justify-center">
        {features.map((feature, index) => (
          <HexagonCard
            key={index}
            icon={feature.icon}
            title={feature.title}
            description={feature.description}
          />
        ))}
      </div>
    </section>
  );
};

export default WhyChooseUs;
